#!/usr/bin/env node

const yargs = require("yargs/yargs");
const { hideBin } = require("yargs/helpers");
// const DigestIt = require("./index"); // Importando a classe DigestIt

// Configurar o yargs para gerenciar comandos e opes
const argv = yargs(hideBin(process.argv))
  .command("atom", "Processa dados em formato Atom", {
    rss: {
      description: "Ativa a opo RSS",
      alias: "rr",
      type: "boolean",
    },
  })
  .command(
    "help",
    "Exibe as opes disponveis",
    () => {},
    (argv) => {
      console.log(`
      Uso: digest [opes]

      Comandos disponveis:
        atom               Processa dados em formato Atom
        help               Exibe as opes disponveis

      Opes:
        -r, --rss          Ativa a opo RSS
        -h, --help         Exibe esta ajuda
    `);
    }
  )
  .help().argv;

// Placeholder para futuras funes
if (argv._.includes("atom")) {
  console.log("Comando 'atom' processado.");
  console.log(argv);

  if (argv.r) {
    console.log("A opo RSS est ativada.");
  }

  // Criando uma instncia de DigestIt
  //   const digest = new DigestIt(1, 2);
  console.log("digest.add()"); // Chamando o mtodo add para exemplo
} else if (argv._.includes("help")) {
  // O comando help  tratado pela funo de callback do yargs
  // No  necessrio cdigo adicional aqui
  console.log("reconhecido 'digest --help'.");
} else {
  console.log("Comando no reconhecido. Use 'digest --help' para ver opes.");
}
